// Time
export const TIME = {
  // 
  TODAY_IN_THIS_YEAR:
    Math.ceil(
      (new Date() - new Date(new Date().getFullYear().toString())) /
        (24 * 60 * 60 * 1000)
    ) + 1,
  // 
  fromNow: function (date) {
    return moment(date, "YYYY-MM-DD HH:mm:ss").fromNow();
  },
  format: function (date) {
    return date.format("YYYY-MM-DD HH:mm:ss");
  },
};

// HTTP
// GET
export const HTTP = {
  get: async function (url) {
    let web = await fetch(url);
    let json = await web.json();
    // CONSOLE.d(url);
    // CONSOLE.i(json);
    return json;
  },
  post: async function (url, body) {
    let web = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    });
    let json = await web.json();
    // CONSOLE.i(json);
    // CONSOLE.d(url);
    // CONSOLE.w(body);
    return json;
  },
};

export const RegExp = {
  isEmpty: function (string) {
    return string == null || string == undefined || string == "";
  },
  isQQ: function (string) {
    if (/\d{5,11}$/.test(string)) {
      return true;
    } else {
      notification.open(NOTIFICATIONS.errorQQ);
      return false;
    }
  },
  isName: function (string) {
    if (string.length >= 1 && string.length <= 16) {
      return true;
    } else {
      notification.open(NOTIFICATIONS.errorName);
      return false;
    }
  },
  isWeb: function (string) {
    if (
      /^((https|http|ftp|rtsp|mms){0,1}(:\/\/){0,1})www\.(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/i.test(
        string
      )
    ) {
      return true;
    } else {
      notification.open(NOTIFICATIONS.errorWeb);
      return false;
    }
  },
  isDiscussContent: function (string) {
    if (string.length >= 1) {
      return true;
    } else {
      notification.open(NOTIFICATIONS.errorDiscussContent);
      return false;
    }
  },
  isBase64Image(string) {
    return /^\s*data:([a-z]+\/[a-z0-9-+.]+(;[a-z-]+=[a-z0-9-]+)?)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@\/?%\s]*?)\s*$/i.test(
      string
    );
  },
  filterHTMLTag(msg) {
    var msg = msg.replace(/<\/?[^>]*>/g, ""); //HTML Tag
    msg = msg.replace(/[|]*\n/, ""); //
    msg = msg.replace(/&nbsp;/g, ""); //npsp
    return msg;
  },
  filterClassEleImg(string) {
    return string.replace(/eleImg/g, "biaoqing");
  },
  formatEditorCode2PrismCode(string) {
    let text = string;
    let formatCodeDiv = document.createElement("formatCodeStart");
    formatCodeDiv.innerHTML = text;
    let codes = formatCodeDiv.getElementsByTagName("pre");
    for (let i = 0; i < codes.length; i++) {
      let language = codes[i].getAttribute("type");
      let codeDiv = document.createElement("formatCodeEnd");
      let codeChildDiv = document.createElement("code");
      codeChildDiv.style.backgroundColor = "#f6f6f6";
      codeChildDiv.style.borderRadius = "8px";
      codeChildDiv.innerHTML = Prism.highlight(
        codes[i].innerText,
        Prism.languages.javascript,
        language
      );
      codeDiv.appendChild(codeChildDiv);
      text = text.replace(codes[i].innerHTML, codeDiv.innerHTML);
    }
    return text;
  },
  getBrowser(userAgent) {
    let userAgents = [
      { regex: /.*rv\:.*/, name: "Microsoft Edge" },
      { regex: /MSIE\s([^\s|;]+)/i, name: "Microsoft Internet Explorer" },
      { regex: /FireFox\/([^\s]+)/i, name: "Firefox" },
      { regex: /Maxthon([\d]*)\/([^\s]+)/i, name: "Aoyou" },
      { regex: /SE 2([a-zA-Z0-9.]+)#i/, name: "Sougou" },
      { regex: /360([a-zA-Z0-9.]+)#i/, name: "360" },
      { regex: /Edge([\d]*)\/([^\s]+)/i, name: "Microsoft Edge" },
      { regex: /UC/i, name: "UC web" },
      { regex: /OPR/i, name: "Opera" },
      { regex: /MicroMesseng/i, name: "Wechat" },
      { regex: /WeiBo/i, name: "Weibo" },
      { regex: /QQ/i, name: "QQ" },
      { regex: /QQBrowser\/([^\s]+)/i, name: "QQ" },
      { regex: /BAIDU/i, name: "Baidu" },
      { regex: /LBBROWSER/i, name: "Liebao" },
      { regex: /TheWorld/i, name: "The window of world" },
      { regex: /UBrowser/i, name: "UC web" },
      { regex: /2345Explorer/i, name: "2345" },
      { regex: /Opera[\s|\/]([^\s]+)/i, name: "Opera" },
      { regex: /Chrome([\d]*)\/([^\s]+)/i, name: "Google Chrome" },
      { regex: /safari\/([^\s]+)/i, name: "Apple safar" },
    ];
    let index = userAgents.findIndex((item) => item.regex.test(userAgent));
    return index < 0 ? "" : `${userAgents[index].name} `;
  },
  getOS(userAgent) {
    let userAgents = [
      {
        regex: /win/i,
        name: "Windows",
        image: require("./images/Device_windows.png"),
      },
      {
        regex: /android/i,
        name: "Android",
        image: require("./images/Device_android.png"),
      },
      {
        regex: /ubuntu/i,
        name: "Ubuntu",
        image: require("./images/Device_linux.png"),
      },
      {
        regex: /linux/i,
        name: "Linux",
        image: require("./images/Device_linux.png"),
      },
      {
        regex: /iPhone/i,
        name: "iPhone",
        image: require("./images/Device_iOS.png"),
      },
      {
        regex: /iPad/i,
        name: "iPad",
        image: require("./images/Device_iOS.png"),
      },
      {
        regex: /mac/i,
        name: "Mac OS",
        image: require("./images/Device_iOS.png"),
      },
    ];
    let index = userAgents.findIndex((item) => item.regex.test(userAgent));
    return index < 0 ? null : userAgents[index];
  },
  getParamByParesingQueryString(url, key) {
    let params = queryString.parse(url);
    return params[key];
  },
};

